'use strict';

var React = require('react');
var Timer = require('timer.react')["default"];
var App = React.createClass({
    displayName: 'App',
    getInitialState: function getInitialState() {
        return {
            countdownSec: Timer.cacheSec('some123'),
            ajaxBusy: false
        };
    },

    render: function render() {
        var self = this;
        return require("react").createElement(
            'div',
            null,
            require("react").createElement(
                Timer,
                {
                    sec: 5,
                    cache: 'some123',
                    className: 'm-btn',
                    timingClassName: 'm-btn--timing',
                    start: function start(_start) {
                        console.log('click timer');
                        if (self.state.ajaxBusy) {
                            return;
                        }
                        self.setState({
                            ajaxBusy: true
                        });
                        // mock ajax POST /ajax/send_email
                        setTimeout(function () {
                            self.setState({
                                ajaxBusy: false
                            });
                            _start();
                            console.log('start timer');
                        }, 1000);
                    },
                    onChange: function onChange(date) {
                        if (date.sec) {
                            self.setState({
                                countdownSec: date.sec
                            });
                        } else {
                            self.setState({
                                countdownSec: 0
                            });
                        }
                    }
                },
                self.state.countdownSec === 0 ? "Send" : self.state.countdownSec + 's'
            ),
            'Click and refresh the page'
        );
    }
});
module.exports = App;


